<template>
	<view class="content">
		<view class="invoice_list_box">
			<view class="invoice" v-for="(item, index) in invoiceList" :key="index">
				<view class="head">
					<view class="left">{{diaplayTime(item.uptime)}}</view>
					<view class="right">
						<span v-if="item.status">已开票</span>
						<span v-else class="wight">等待开票</span>
					</view>
				</view>
				<view class="info">
					<view class="label">名称：{{item.company_name}}</view>
					<view class="label">税号：{{item.company_tax}}</view>
					<view class="label">金额：¥{{item.tax_money}}</view>
					<view class="label">邮箱：{{item.user_email}}</view>
				</view>
				<view class="foot">
					<view v-if="item.status" class="button active" @click="getPriveTap(item.pic)"><aicon type="view" size="16"></aicon>查看发票</view>
					<view v-else class="button" @click="getChatTap()"><aicon type="service-fill" size="16"></aicon>联系客服</view>
				</view>
			</view>
			<view v-if="invoiceList.length===0" class="none_list_box">
				<image src="../../static/images/none_02.png" mode="aspectFill"></image>
				<span>暂无开票申请</span>
			</view>
		</view>
		
		<view class="footer_after_box">
			<view class="after"></view>
		</view>
		<view class="footer">
			<view class="button" @click="getPathTap('/pages/user/invoice')"><aicon type="ticket-fill" size="20"></aicon>申请开票</view>
		</view>
	</view>
</template>

<script>
	const app = getApp();
	export default {
		data() {
			return {
				template: uni.getStorageSync('__ddminiTemplateConfig'),
				invoiceList: []
			}
		},
		onShow() {
			let self = this;
			self.getInvoiceassistantList();
		},
		methods: {
			getInvoiceassistantList(){
				let self = this;
				let postData = {
					page: 1,
					sizes: 50
				}
				self.Get(self.Url.invoiceassistantList, postData).then(res => {
					if(res.code === 0){
						self.invoiceList = res.data.list;
					}
				})
			},
			getPriveTap(pic){
				let self = this;
				if(!pic){
					uni.showModal({
						content: '发票已发送至您的邮箱，请注意查收！',
						showCancel: false,
						confirmColor: self.template.color.temp_color
					})
					return
				}
				uni.previewImage({
					urls: [pic],
					current: pic
				})
			},
			getChatTap(){
				let self = this;
				uni.openCustomerServiceChat({
					extInfo: {
						url: app.globalData.customerUrl
					},
					corpId: app.globalData.customerId,
					success(ces){}
				})
			},
			getPathTap(path){
				app.globalData.getPathTap(path);
			}
		}
	}
</script>

<style lang="less">
	page{
		background-color: #fff;
	}
	.invoice_list_box{
		width: 100%;
		padding-top: 40rpx;
		.invoice{
			width: 85%;
			margin: 0 auto;
			margin-bottom: 50rpx;
			border: 1px solid #2e6cf630;
			box-shadow: 10rpx 10rpx 0 #2e6cf620;
			border-radius: 20rpx;
			padding-bottom: 14rpx;
			.head{
				width: 90%;
				margin: 0 auto;
				display: flex;
				align-items: center;
				height: 100rpx;
				justify-content: space-between;
				.left{
					color: #999;
					font-size: 28rpx;
				}
				.right{
					span{
						border: 1px solid #07c160;
						color: #07c160;
						height: 46rpx;
						font-size: 24rpx;
						padding: 0 12rpx;
						border-radius: 10rpx;
						display: flex;
						align-items: center;
						justify-content: center;
					}
					.wight{
						color: #ff9900;
						border-color: #ff9900;
					}
				}
			}
			.info{
				width: 90%;
				margin: 0 auto;
				color: #1c1c1c;
				line-height: 50rpx;
				.label{
					width: 100%;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
				}
			}
			.foot{
				width: 90%;
				margin: 0 auto;
				height: 100rpx;
				display: flex;
				align-items: center;
				justify-content: center;
				.button{
					width: 280rpx;
					height: 58rpx;
					display: flex;
					align-items: center;
					justify-content: center;
					background-color: #fff;
					color: #2e6cf6;
					border-radius: 100rpx;
					font-size: 28rpx;
					border: 1px solid #2e6cf6;
					aicon{
						width: 32rpx;
						height: 32rpx;
						display: flex;
						align-items: center;
						margin-right: 10rpx;
					}
				}
				.active{
					background-color: #2e6cf6;
					color: #fff;
				}
			}
		}
	}
	
	
	.footer_after_box{
		width: 100%;
		padding-bottom: env(safe-area-inset-bottom);
		.after{
			width: 100%;
			height: 150rpx;
		}
	}
	.footer{
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 130rpx;
		.button{
			width: 80%;
			margin: 0 auto;
			height: 90rpx;
			background-color: #2e6cf6;
			color: #fff;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 32rpx;
			border-radius: 100rpx;
			font-weight: bold;
			position: relative;
			animation: bounce-down 2s linear infinite;
			aicon{
				width: 40rpx;
				height: 40rpx;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-right: 10rpx;
			}
		}
	}
	@keyframes bounce-down {
	  25% {transform: scale(1.07);}
	  50%, 100% {transform: scale(1);}
	  75% {transform: scale(1.07);}
	}
</style>
